.team {
  font-family: 'Inter', sans-serif;
  padding: 128px;
  background-color: $whiteSmoke;

  &__headline {
    font-size: 72px;
    font-style: normal;
    font-weight: 700;
    line-height: 100%;
    letter-spacing: -3.6px;
    margin: 0;
    padding: 48px 0;
  }

  &__copy {
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: 140%;
    letter-spacing: -0.6px;
    padding: 24px 0;

    & a {
      color: inherit;
      text-decoration: underline;
    }
  }

  &__button {
    background-color: transparent;
    padding: 12px 24px;
    text-transform: capitalize;
    color: $monochromeBlack;
  }

  &__max-width {
    max-width: 1300px;
    margin: auto;
    padding: 0 25px;
  }

  &__grid {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: flex-start;
    align-self: stretch;
  }

  &__card {
    width: 21%;
    padding: 30px 0;

    &__image {
      margin: 0;
      width: 200px;
      height: 200px;
    }

    // &__image:after {
    //   content: '\A';
    //   position: absolute;
    //   width: 100%;
    //   height: 100%;
    //   top: 0;
    //   left: 0;
    //   background-color: rgba(233, 215, 112, 0.2);
    //   border-radius: 5px;
    //   opacity: 0;
    //   cursor: pointer;
    //   transition: all 0.5s;
    //   -webkit-transition: all 0.5s;
    // }

    // &__image:hover:after {
    //   opacity: 1;
    // }

    &__name {
      padding: 24px 0 8px;
    }

    &__title {
      font-size: 12px;
      font-style: normal;
      font-weight: 400;
      line-height: 150%;
      text-transform: uppercase;
    }
  }
}
